import React from 'react'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'

import Layout from '../components/layout'
import Markdown from '../components/Markdown'
import Container from '@material-ui/core/Container'

import BackButton from '../components/BackButton'
import { Typography } from '@material-ui/core'

export default function Template({ data, ...props }) {
  const recipe = data.recipe
  console.log('pageProps', props, recipe)
  const image = (
    data.images.edges.find(({ node: image }) => {
      const checkPath = `/${image.relativePath}`
      return checkPath.includes(recipe.frontmatter.path.split('/recipes')[1])
    }) || {}
  ).node
  console.log('Template -> image', image)
  return (
    <Layout>
      <Container maxWidth="md">
        {/* {JSON.stringify(recipe.headings)} */}
        <BackButton style={{ marginTop: 50 }} to="/recipes">
          Back to Recipes
        </BackButton>
        <Typography
          variant="h2"
          style={{
            marginTop: 20,
          }}
        >
          {recipe.frontmatter.title}
        </Typography>
        <Typography
          variant="h4"
          style={{
            marginTop: 15,
            marginBottom: 15,
            fontSize: '.8em',
            color: '#9fa7a7',
            fontWeight: '400',
          }}
        >
          Posted by {recipe.frontmatter.author} on {recipe.frontmatter.date}
        </Typography>
        {/* <div>
          <Img
            // fluid={image.childImageSharp.fluid}
            fixed={image.childImageSharp.fixed}
            alt={`Image of ${recipe.frontmatter.title}`}
          />
        </div> */}
        <Markdown
          // disableMapping
          data={recipe}
          // rehypeConfig={{
          //   ingredients: Ingredients,
          //   ingredient: Ingredient,
          //   instructions: Instructions,
          //   instruction: Instruction,
          // }}
        />
      </Container>
    </Layout>
  )
}

export const recipeQuery = graphql`
  query RecipeByPath($path: String!) {
    images: allFile(
      filter: {
        sourceInstanceName: { eq: "recipes" }
        relativePath: { regex: "/(title.png)/" }
      }
    ) {
      edges {
        node {
          relativePath
          relativeDirectory
          name
          extension
          dir
          modifiedTime
          childImageSharp {
            # Specify the image processing specifications right in the query.
            # Makes it trivial to update as your page's design changes.
            fixed(width: 250) {
              ...GatsbyImageSharpFixed
            }
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }

    recipe: markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      htmlAst
      headings {
        value
      }
      frontmatter {
        path
        title
        author
        date
      }
    }
  }
`
